* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
main {
  text-align: center;
}
kbd {
  border: 1px solid red;
  width: 4em;
  height: 4em;
  display: inline-block;
  text-transform: uppercase;
  position: relative;
}
kbd > button {
  position: absolute;
  right: 0;
  bottom: 0;
  display: none;
}
kbd:hover > button {
  display: inline-block;
}
#mainXXX > div:nth-child(2) {
  margin-left: 2em;
}
#mainXXX > div:nth-child(3) {
  margin-left: 3em;
}
